<template>
  <!--  语言规范屏蔽-->
  <!-- eslint-disable -->
  <div class="graph">
    <div class="wrapper" id="header">多功能监测浮标信息管理系统</div>
    <div class="nav">
      <router-link to="/index">表格&地图</router-link>    |
      <router-link to="/graph">图表展示</router-link>  |
      <router-link to="/user">用户中心</router-link>
<!--      <router-link to="/camera">摄像中心</router-link>-->
    </div>
    <div class="table">
      <el-table :data="value"
                highlight-current-row
                class="el_table"
                height="567"
                border
                style="width: 100%"
                :header-cell-style="{'text-align':'center',background:'#eef1f6',color:'#606266'}"
                :cell-style="{'text-align':'center'}"
                header-cell-class-name="header-cell-color" v-loading="listLoading">
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="dev_id" label="dev_id" width="100">
          <template  slot="header" slot-scope="scope">
            <el-dropdown trigger="hover" @command="handleCommand"><span class="el-dropdown-link">dev_id<i class="el-icon-arrow-down  el-icon--right el-icon--right"></i></span>
              <el-dropdown-menu slot="dropdown">
                <el-radio-group v-model="radio">
                  <el-dropdown-item command=10038 v-for="item in options" :command=item.value>
                    <el-radio :key="item.value" :value="item.value" :label="item.value">{{item.value}}
                    </el-radio>
                  </el-dropdown-item>
                </el-radio-group>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
          <template slot-scope="scope">{{scope.row.dev_id}}
          </template>>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="datetime" label="时间" width="120"></el-table-column>
        <el-table-column prop="tmp" label="温度(℃)" width="100"></el-table-column>
        <el-table-column prop="ecm" label="电导率(mS)" width="100"></el-table-column>
        <el-table-column prop="phg" label="pH" width="80"></el-table-column>
        <el-table-column prop="rdo" label="溶解氧(mg/L)" width="120"></el-table-column>
        <el-table-column prop="tss" label="浊度(NTU)" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="echo" label="回波" width="110"></el-table-column>
        <el-table-column prop="chl" label="叶绿素(ug/L)" width="120"></el-table-column>
        <el-table-column prop="ecs" label="盐度(PSU)" width="120"></el-table-column>
        <el-table-column prop="lng" label="经度(°)" width="100"></el-table-column>
        <el-table-column prop="lat" label="纬度(°)" width="100"></el-table-column>
      </el-table>
    </div>
    <div class="camera"><h4>摄像头{{ dev_id }}</h4>
      <div class="image">
        <el-image :src="src"></el-image>
      </div>
    </div>
    <div class="btnexcel"><el-button type="primary" @click="exportExcel">导出Excel</el-button></div>
    <div class="btncamera">
      <el-switch
          v-model="value_2"
          active-text="摄像头开启"
          active-color="#13ce66"
          @change="switchChange(value_2,dev_id)"
          inactive-color="#ff4949">
      </el-switch>
    </div>
    <div class="charts">
      <div class="tmp" style="margin-left: -300px;margin-top: 700px"><h4>温度变化趋势</h4></div>
      <div id="tmpcharts" style="height: 500px;width: 1200px;margin-top: -20px"></div>
      <div class="ecm" style="margin-left: -300px;margin-top: 100px"><h4>电导率变化趋势</h4></div>
      <div id="ecmcharts" style="height: 500px;width: 1200px;margin-top: -20px"></div>
      <div class="phg" style="margin-left: -300px;margin-top: 100px"><h4>pH变化趋势</h4></div>
      <div id="phgcharts" style="height: 500px;width: 1200px;margin-top: -20px"></div>
      <div class="rdo" style="margin-left: -300px;margin-top: 100px"><h4>溶解氧变化趋势</h4></div>
      <div id="rdocharts" style="height: 500px;width: 1200px;margin-top: -20px"></div>
      <div class="tss" style="margin-left: -300px;margin-top: 100px"><h4>浊度变化趋势</h4></div>
      <div id="tssbcharts" style="height: 500px;width: 1200px;margin-top: -20px"></div>
      <div class="echo" style="margin-left: -300px;margin-top: 100px"><h4>回波强度</h4></div>
      <div id="echocharts" style="height: 600px;width: 1400px;margin-top: -20px"></div>
      <div class="chl" style="margin-left: -300px;margin-top: 100px"><h4>叶绿素变化趋势</h4></div>
      <div id="chlharts" style="height: 500px;width: 1200px;margin-top: -20px"></div>
      <div class="ecs" style="margin-left: -300px;margin-top: 100px"><h4>盐度变化趋势</h4></div>
      <div id="ecscharts" style="height: 500px;width: 1200px;margin-top: -20px"></div>
    </div>
<!--    <div class="nav_bottom">-->
<!--      <li style="list-style-type: none;display: inline-block;width: 200px;margin-left:300px"><a href="#" style="text-decoration:none;">关于我们</a></li>-->
<!--      <li style="list-style-type: none;display: inline-block;width: 200px"><a href="#" style="text-decoration:none;">帮助中心</a></li>-->
<!--      <li style="list-style-type: none;display: inline-block;width: 200px"><a href="#" style="text-decoration:none;">友情链接</a></li>-->
<!--      <li style="list-style-type: none;display: inline-block;width: 200px"><a href="#" style="text-decoration:none;">快速导航</a></li>-->
<!--      <li style="list-style-type: none;display: inline-block;width: 200px;margin-left: 100px"><a href="#top" style="text-decoration:none;">回到顶部</a></li>-->
<!--    </div>-->
    <div class="copyright_width">Copyright © 2020 All Rights Reserved </div>
  </div>
</template>
<script>
import axios from "axios";
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
// let dev_id = this.$router.query.data
export default {
  name: "Graph",
  data() {
    return {
      value: null,
      value_2: false,
      options: null,
      dev_id: null,
      src: null
    }
  },
  created() {
    const _this = this;
    let datetimeArray = new Array();
    let tmpArray = new Array();
    let ecmArray = new Array();
    let phgArray = new Array();
    let rdoArray = new Array();
    let tssArray = new Array();
    let echoArray = new Array();
    let chlArray = new Array();
    let ecsArray = new Array();
    let lngArray = new Array();
    let latArray = new Array();
    let dev_id = this.$route.params.dev_id
    if(dev_id==null){
      dev_id = 10038;
    }
    axios.get('http://127.0.0.1:8181/datatable/findAll/' + dev_id).then(function(resp){
      _this.value = resp.data;
      _this.dev_id = dev_id;
      for (let i=0;i<resp.data.length;i++)
      {
        datetimeArray[i] = resp.data[i].datetime;
        tmpArray[i] = resp.data[i].tmp;
        ecmArray[i] = resp.data[i].ecm;
        phgArray[i] = resp.data[i].phg;
        rdoArray[i] = resp.data[i].rdo;
        tssArray[i] = resp.data[i].tss;
        echoArray[i] = resp.data[i].echo;
        chlArray[i] = resp.data[i].chl;
        ecsArray[i] = resp.data[i].ecs;
        lngArray[i] = resp.data[i].lng;
        latArray[i] = resp.data[i].lat;
      }
      _this.tmpCharts(datetimeArray,tmpArray);
      _this.ecmCharts(datetimeArray,ecmArray);
      _this.phgCharts(datetimeArray,phgArray);
      _this.rdoCharts(datetimeArray,rdoArray);
      _this.tssCharts(datetimeArray,tssArray);
      _this.echoCharts(datetimeArray,echoArray);
      _this.chlCharts(datetimeArray,chlArray);
      _this.ecsCharts(datetimeArray,ecsArray);
    });
    axios.get('http://127.0.0.1:8181/datatable/findAll/devId').then(function(resp){
      var filters = new Array();
      var newfilters = new Array();
      for(let i = 10038 ; i < 10050 ; i++)
      {
        filters = {"value":i}
        newfilters = newfilters.concat(filters)
      }
      _this.options = newfilters
    })
  },
  methods:{
    handleCommand(dev_id) {
      const _this = this;
      _this.dev_id = dev_id;
      let datetimeArray = new Array();
      let tmpArray = new Array();
      let ecmArray = new Array();
      let phgArray = new Array();
      let rdoArray = new Array();
      let tssArray = new Array();
      let echoArray = new Array();
      let chlArray = new Array();
      let ecsArray = new Array();
      let lngArray = new Array();
      let latArray = new Array();
      axios.get('http://127.0.0.1:8181/datatable/findAll/' + dev_id).then(function(resp){
        _this.value = resp.data;
        for (let i=0;i<resp.data.length;i++)
        {
          datetimeArray[i] = resp.data[i].datetime;
          tmpArray[i] = resp.data[i].tmp;
          ecmArray[i] = resp.data[i].ecm;
          phgArray[i] = resp.data[i].phg;
          rdoArray[i] = resp.data[i].rdo;
          tssArray[i] = resp.data[i].tss;
          echoArray[i] = resp.data[i].echo;
          chlArray[i] = resp.data[i].chl;
          ecsArray[i] = resp.data[i].ecs;
          lngArray[i] = resp.data[i].lng;
          latArray[i] = resp.data[i].lat;
        }
        _this.tmpCharts(datetimeArray,tmpArray);
        _this.ecmCharts(datetimeArray,ecmArray);
        _this.phgCharts(datetimeArray,phgArray);
        _this.rdoCharts(datetimeArray,rdoArray);
        _this.tssCharts(datetimeArray,tssArray);
        _this.echoCharts(datetimeArray,echoArray);
        _this.chlCharts(datetimeArray,chlArray);
        _this.ecsCharts(datetimeArray,ecsArray);

      });
    },
    renderLastHeader(h) {
      //下拉框选项
      //[{text: "10038", 'value': "10038"}, {text: "10039", 'value': "10039"}, {text: "10040", 'value': "10040"}, {text: "10041", 'value': "10041"}];
      // axios.get('http://localhost:8181/datatable/findAll/devId').then(function(resp){
      // })
      var filters = new Array();
      var newfilters = new Array();
      for(let i = 10038 ; i < 10050 ; i++)
      {
          filters = {"text":i,"value":i}
          newfilters = newfilters.concat(filters)
      }
      //下拉框内容包裹在一个div里面
      return h('div',{
      },[
        h("span",{//div里面有一个文字提示：下拉框所属内容
            style: {
            },
            class:'level-font-class'
        },),
        h('el-select',{//el-select实现下拉框
            on:{
              input: (value) => {//随着下拉框的不同，文字框里的内容随之变化
                this.logLevel = value;
              },
            },
            props:{
              // 文字框的内容取决于这个value，如果value不存在，会报错
              value:this.logLevel,
            },
          }, [
            //下拉框里面填充选项，通过filters遍历map，为每一个选项赋值。
            newfilters.map(item =>
            {
            return h("el-option", {
            props:
                {
                    value: item.value,
                    label: item.text
                }
            })
            })
        ])
      ])
    },
    exportExcel() {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector('.table'))
      /* get binary string as output */
      var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'sheet.xlsx')
      } catch (e)
      { if (typeof console !== 'undefined') console.log(e, wbout) }
      return wbout
    },
    switchChange(value_2,dev_id){
      const _this = this;
      let src = null;
      if(value_2 == true){
        src = 'https://cube.elemecdn.com/6/94/4d3ea53c084bad6931a56d5158a48jpeg.jpeg';
        _this.src = src;
      }
      else{
        _this.src = null;
      }
    },
    tmpCharts(datetimeArray,tmpArray){
      const mychart = this.$echarts.init(document.getElementById('tmpcharts'))
      const option = {
        title: {
          padding: [20,30,150,95],
          nameLocation: 'middle',
          text: '温度(℃)',
          textStyle: {
            fontSize:14,
          },
        },
        tooltip: {},
        // color:'#00eebe',
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          boundaryGap: false,
          data: datetimeArray,
          axisLabel:{
            showMaxLabel: true,
          },
        },
        yAxis: {
          splitLine:{
            show: false
          }
        },
        series: [{
          name: '温度(℃)',
          type: 'line',
          data: tmpArray,
          smooth:true,
          symbolSize:5,
          symbol:'circle',
          itemStyle:{
            normal:{
              label:{
                show: true,
              },
              lineStyle:{
                color:'#eed600'
              }
            }
          }
        }]
      }
      mychart.setOption(option)
    },
    ecmCharts(datetimeArray,ecmArray){
      const mychart = this.$echarts.init(document.getElementById('ecmcharts'))
      const option = {
        title: {
          padding: [20,30,150,85],
          nameLocation: 'middle',
          text: '电导率(mS)',
          textStyle: {
            fontSize:14,
          },
        },
        tooltip: {},
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        legend: {
          data:['温度(℃)']
        },
        xAxis: {
          boundaryGap: false,
          data: datetimeArray,axisLabel:{
            showMaxLabel: true,
          },
        },
        yAxis: {
          splitLine:{
            show: false
          }
        },
        series: [{
          name: '电导率(mS)',
          type: 'line',
          data: ecmArray,
          smooth:true,
          symbolSize:5,
          symbol:'circle',
          itemStyle:{
            normal:{
              label:{
                show: true,
              },
              lineStyle:{
                color:'#eed600'
              }
            }
          }
        }]
      }
      mychart.setOption(option)
    },
    phgCharts(datetimeArray,phgArray){
      const mychart = this.$echarts.init(document.getElementById('phgcharts'))
      const option = {
        title: {
          padding: [20,30,150,110],
          nameLocation: 'middle',
          text: 'pH',
          textStyle: {
            fontSize:14,
          },
        },
        tooltip: {},
        // legend: {
        //   data:['温度(℃)']
        // },
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          boundaryGap: false,
          data: datetimeArray,axisLabel:{
            showMaxLabel: true,
          },
        },
        yAxis: {
          splitLine:{
            show: false
          }
        },
        series: [{
          name: 'pH',
          type: 'line',
          data: phgArray,
          smooth:true,
          symbolSize:5,
          symbol:'circle',
          itemStyle:{
            normal:{
              label:{
                show: true,
              },
              lineStyle:{
                color:'#eed600'
              }
            }
          }
        }]
      }
      mychart.setOption(option)
    },
    rdoCharts(datetimeArray,rdoArray){
      const mychart = this.$echarts.init(document.getElementById('rdocharts'))
      const option = {
        title: {
          padding: [20,30,150,90],
          nameLocation: 'middle',
          text: '溶解氧(mg/L)',
          textStyle: {
            fontSize:14,
          },
        },
        tooltip: {},
        // legend: {
        //   data:['温度(℃)']
        // },
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          boundaryGap: false,
          data: datetimeArray,axisLabel:{
            showMaxLabel: true,
          },
        },
        yAxis: {
          splitLine:{
            show: false
          }
        },
        series: [{
          name: '溶解氧(mg/L)',
          type: 'line',
          data: rdoArray,
          smooth:true,
          symbolSize:5,
          symbol:'circle',
          itemStyle:{
            normal:{
              label:{
                show: true,
              },
              lineStyle:{
                color:'#eed600'
              }
            }
          }
        }]
      }
      mychart.setOption(option)
    },
    tssCharts(datetimeArray,tssArray){
      const mychart = this.$echarts.init(document.getElementById('tssbcharts'))
      const option = {
        title: {
          padding: [20,30,150,90],
          nameLocation: 'middle',
          text: '浊度(NTU)',
          textStyle: {
            fontSize:14,
          },
        },
        tooltip: {},
        // legend: {
        //   data:['温度(℃)']
        // },
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          boundaryGap: false,
          data: datetimeArray,axisLabel:{
            showMaxLabel: true,
          },
        },
        yAxis: {
          splitLine:{
            show: false
          }
        },
        series: [{
          name: '浊度(NTU)',
          type: 'line',
          data: tssArray,
          smooth:true,
          symbolSize:5,
          symbol:'circle',
          itemStyle:{
            normal:{
              label:{
                show: true,
              },
              lineStyle:{
                color:'#eed600'
              }
            }
          }
        }]
      }
      mychart.setOption(option)
    },
    echoCharts(datetimeArray,echoArray){
      //逗号表达式数据拆分
      var series = [];
      const mychart = this.$echarts.init(document.getElementById('echocharts'))
      let echoList = new Array();
      var left = -700;
      for(let i = 10;i < echoArray.length;i++)
      {

        left = left + 110;
        echoList = echoArray[i].split(',')
        series.push(
            {
              type:'funnel',
              left: left,
              top: 60,
              //x2: 80,
              bottom: 60,
              width: '100%',
              // height: {totalHeight} - y - y2,
              min: 0,
              max: 100,
              minSize: '1%',
              maxSize: '1%',
              sort: 'none',// 排序，none
              gap: 0,
              label: {
                show: true,
                position: 'right',
                formatter: '{c}',
              },
              labelLine: {
                length: 10,
                lineStyle: {
                  width: 1,
                  type: 'solid'
                }
              },
              itemStyle: {
                borderColor: '#fff',
                borderWidth: 0
              },
              emphasis: {
                label: {
                  fontSize: 20
                }
              },
              data: echoList
            }
        )
      }
      const option = {
        title: {
          padding: [20,30,150,0],
          nameLocation: 'middle',
          text: '水深',
          textStyle: {
            fontSize:14,
          },
        },
        color: ['#8b0000','#ff0000', '#ffa500', '#ffff00', '#32cd32', '#00ffff','#0000ff','#000080'],
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        tooltip: {
        },
        // xAxis: {
        //   boundaryGap: false,
        //   data: datetimeArray,
        // },
        series: series,
      }
      mychart.setOption(option)
    },
    chlCharts(datetimeArray,chlArray){
      const mychart = this.$echarts.init(document.getElementById('chlharts'))
      const option = {
        title: {
          padding: [20,30,150,90],
          nameLocation: 'middle',
          text: '叶绿素(ug/L)',
          textStyle: {
            fontSize:14,
          },
        },
        tooltip: {},
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          boundaryGap: false,
          data: datetimeArray,axisLabel:{
            showMaxLabel: true,
          },
        },
        yAxis: {
          splitLine:{
            show: false
          }
        },
        series: [{
          name: '叶绿素(ug/L)',
          type: 'line',
          data: chlArray,
          smooth:true,
          symbolSize:5,
          symbol:'circle',
          itemStyle:{
            normal:{
              label:{
                show: true,
              },
              lineStyle:{
                color:'#eed600'
              }
            }
          }
        }]
      }
      mychart.setOption(option)
    },
    ecsCharts(datetimeArray,ecsArray){
      const mychart = this.$echarts.init(document.getElementById('ecscharts'))
      const option = {
        title: {
          padding: [20,30,150,90],
          nameLocation: 'middle',
          text: '盐度(PSU)',
          textStyle: {
            fontSize:14,
          },
        },
        tooltip: {},
        toolbox: {
          show: true,
          orient: 'vertical',
          top: 'center',
          right:40,
          feature: {
            dataView: {readOnly: false},
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          boundaryGap: false,
          data: datetimeArray,axisLabel:{
            showMaxLabel: true,
          },
        },
        yAxis: {
          splitLine:{
            show: false
          }
        },
        series: [{
          name: '盐度(PSU)',
          type: 'line',
          data: ecsArray,
          smooth:true,
          symbolSize:5,
          symbol:'circle',
          itemStyle:{
            normal:{
              label:{
                show: true,
              },
              lineStyle:{
                color:'#eed600'
              }
            }
          }
        }]
      }
      mychart.setOption(option)
    }
  }
}
</script>
<style scoped>
.table {
  margin-left: 40px;
  margin-top: 20px;
  width: 650px;
  float: left;
}
.camera {
  margin-top: 20px;
  margin-right: 30px;
  width: 650px;
  height: 567px;
  float: right;
}
.el-switch {
  height:40px;
  float: right;
  margin-right: 300px;
}
.btnexcel {
  height: 40px;
  width: 40px;
