    <div class="table_2">
      <el-table :data="value"
                highlight-current-row
                class="el_table"
                height="567"
                border
                style="width: 100%"
                :header-cell-style="{'text-align':'center',background:'#eef1f6',color:'#606266'}"
                :cell-style="{'text-align':'center'}">
        <el-table-column prop="id" label="id" width="60"></el-table-column>
        <el-table-column prop="dev_id" label="dev_id" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="datetime" label="时间" width="120"></el-table-column>
        <el-table-column prop="tmp" label="温度(℃)" width="100"></el-table-column>
        <el-table-column prop="ecm" label="电导率(mS)" width="100"></el-table-column>
        <el-table-column prop="phg" label="pH" width="80"></el-table-column>
