        <el-table-column prop="tss" label="浊度(NTU)" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" prop="echo" label="回波" width="110"></el-table-column>
        <el-table-column prop="chl" label="叶绿素(ug/L)" width="120"></el-table-column>
        <el-table-column prop="ecs" label="盐度(PSU)" width="120"></el-table-column>
        <el-table-column prop="lat" label="纬度(°)" width="100"></el-table-column>
        <el-table-column prop="lng" label="经度(°)" width="100"></el-table-column>
      </el-table>
      <el-button type="primary" class="js_exportExcel2" @click="exportExcel2">数据Excel</el-button>
