<script>
import axios from "axios"
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
let points;
export default {
  name: "Home",
  data() {
    return {
      value: null,
      total: null,
      value_2:null,
      dev:null,
      map:{
        center: {lng: 121.85, lat: 31.22},
        zoom: 10,
        roam: true,
        show: true,
        dragging: true,
        mapStyle: {
          styleJson: [{
            'featureType': 'water',
            'elementType': 'all',
            'stylers': {
              'color': '#99ddff'
            }
          }, {
            'featureType': 'land',
            'elementType': 'all',
            'stylers': {
              'color': '#f3f3f3'
            }
          }, {
            'featureType': 'railway',
            'elementType': 'all',
            'stylers': {
              'visibility': 'on'
            }
          }, {
            'featureType': 'highway',
            'elementType': 'all',
            'stylers': {
              'color': '#fdfdfd'
            }
          }, {
            'featureType': 'highway',
            'elementType': 'labels',
            'stylers': {
              'visibility': 'on'
            }
          }, {
            'featureType': 'arterial',
            'elementType': 'geometry',
            'stylers': {
              'color': '#fefefe'
            }
          }, {
            'featureType': 'arterial',
            'elementType': 'geometry.fill',
            'stylers': {
              'color': '#fefefe'
            }
          }, {
            'featureType': 'poi',
            'elementType': 'all',
            'stylers': {
              'visibility': 'off'
            }
          }, {
            'featureType': 'green',
            'elementType': 'all',
            'stylers': {
              'visibility': 'off'
            }
          }, {
            'featureType': 'subway',
            'elementType': 'all',
            'stylers': {
              'visibility': 'off'
            }
          }, {
            'featureType': 'manmade',
            'elementType': 'all',
            'stylers': {
              'color': '#d1d1d1'
            }
          }, {
            'featureType': 'local',
            'elementType': 'all',
            'stylers': {
              'color': '#d1d1d1'
            }
          }, {
            'featureType': 'arterial',
            'elementType': 'labels',
            'stylers': {
              'visibility': 'off'
            }
          }, {
            'featureType': 'boundary',
            'elementType': 'all',
            'stylers': {
              'color': '#fefefe'
            }
          }, {
            'featureType': 'building',
            'elementType': 'all',
            'stylers': {
              'color': '#d1d1d1'
            }
          }, {
            'featureType': 'label',
            'elementType': 'labels.text.fill',
            'stylers': {
              'color': '#999999'
            }
          }]
        }
      }
    }
  },
  created() {
    let dev_id = 10038;
    const _this = this;
    axios.get('http://127.0.0.1:8181/datatable/findAll').then(function(resp){
      _this.value = resp.data;
    });
    axios.get('http://127.0.0.1:8181/datatable/findAll/' + dev_id).then(function(resp){
      _this.value_2 = resp.data;
    });
    axios.get('http://127.0.0.1:8181/datadev/findAll/').then(function(resp){
      _this.dev = resp.data;
    });

  },
  methods:{
    handler({BMap,map}) {
      map.enableScrollWheelZoom(true);
      const _this = this;
      // 数据库->浮标编号list->遍历list->画坐标点
      for(let dev_id = 10038;dev_id<10050;dev_id++){
        axios.get('http://127.0.0.1:8181/datatable/findAll/' + dev_id).then(function(resp){
          points = resp.data;
          let point = new BMap.Point(points[0].lng, points[0].lat);
          // var icon = new BMap.Icon('http://wx.58haha.cn/1.png', new BMap.Size(20, 32), {
          //   anchor: new BMap.Size(10, 30)
          // });
          // var mkr = new BMap.Marker(new BMap.Point(116.498752,39.900157), {
          //   icon: icon
          // });
          let marker = new BMap.Marker(point,{
          });
          map.addOverlay(marker)
          let label = new BMap.Label(point.lat + '°N'+ ',' + point.lng + '°E' + ',' + '浮标编号' + dev_id,{
            offset: new BMap.Size(20, -10)
          })
          //添加标签
          marker.setLabel(label)
          label.setStyle({
            display: "none",
            color: "black",
            fontSize: "12px",
            height: "20px",
            lineHeight: "20px",
            fontFamily: "微软雅黑",
            maxWidth: "none",
            border: "0"
          });
          // 鼠标经过时
          marker.addEventListener("mouseover", function(e) {
            let label = this.getLabel();
            label.setStyle({
              display: "block"
            });
            let devid = label.content.split(',')[2];
            axios.get('http://127.0.0.1:8181/datatable/findAll/' + devid).then(function(resp){
              _this.value_2 = resp.data
            })
          });
          // 鼠标离开时
          marker.addEventListener("mouseout", function(e) {
            let label = this.getLabel();
            label.setStyle({
              display: "none"
            });
          });
          // 鼠标点击事件
          marker.addEventListener("click", function(e) {
            // console.log(dev_id)
            _this.$router.push({name:'Graph', params:{ dev_id:dev_id } })
          });
        })
      }
    },
    exportExcel() {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector('.table'))
      /* get binary string as output */
      var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'sheet.xlsx')
      } catch (e)
      { if (typeof console !== 'undefined') console.log(e, wbout) }
      return wbout
    },
    exportExcel2() {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector('.table_2'))
      /* get binary string as output */
      var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'sheet.xlsx')
      } catch (e)
      { if (typeof console !== 'undefined') console.log(e, wbout) }
      return wbout
    },
  }
}
</script>
