import Vue from "vue";
import VueRouter from "vue-router";
import Login from "@/views/Login";
import Register from "@/views/Register";
import Index from "../views/Index.vue";
import User from "@/views/User";
import Graph from "@/views/Graph";
// import Camera from "@/views/Camera";
Vue.use(VueRouter);
const routes = [
  {
    path: "/",
    name: "Login",
    component: Login,
  },
  {
    path: "/register",
    name: "Register",
    component: Register,
  },
  {
    path: "/index",
    name: "Index",
    component: Index,
  },
  {
    path: "/graph",
    name: "Graph",
    component: Graph
  },
  {
    path: "/user",
    name: "User",
    component: User
  },
  // {
  //   path: "/camera",
  //   name: "Camera",
  //   component: Camera
  // }
];
const router = new VueRouter({
  mode:'hash',
  routes
});
export default router;
